  }
}


#wrapper-animation {
  position: relative;
  margin-top: -100px;
  z-index: 1000;
}

.animation-image {
  width: 100%;
}

.animation-mask {
  height: 700px;
  width: 800px;
  overflow: hidden;
  display: flex;
  grid-gap: 16px;
  gap: 16px;
  border-radius: 16px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  --animation-offset: 7700px;
  --animation-duration: 45s;
  margin-bottom: 32px;
}

@keyframes changeYup {
  100% {
    transform: translateY(calc(var(--animation-offset)*-1));
  }
}

@keyframes changeYdown {
  100% {
    transform: translateY(var(--animation-offset));
  }
}

.animation-column {
  display: flex;
  flex-direction: column;
  grid-gap: 16px;
  gap: 16px;
  animation-name: changeYup;
  animation-duration: var(--animation-duration);
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
}

.animation-column:nth-child(even) {
  margin-top: calc(var(--animation-offset)*-1);
  animation-name: changeYdown;
  animation-duration: var(--animation-duration);
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-timing-function: linear;
}

@media screen and (max-width: 1350px) {
  .animation-mask {
    --animation-offset: 5500px;
  }
}
@media screen and (max-width: 1000px) {
  .animation-mask {
    --animation-offset: 4000px;
  }
}
@media screen and (max-width: 750px) {
  .animation-mask {
    height: 500px;
    --animation-offset: 3000px;
  }
}
@media screen and (max-width: 550px) {
  .animation-mask {
    height: 400px;
    --animation-offset: 2000px;
  }
}
@media screen and (max-width: 400px) {
  .animation-column {
      grid-gap: 8px;
      gap: 8px;
  }
  .animation-mask {
    --animation-offset: 1000px;
    height: 300px;
    grid-gap: 8px;
    gap: 8px;
  }
}
@media screen and (max-width: 250px) {
  .animation-mask {
    --animation-offset: 500px;
  }
}
@media screen and (max-width: 150px) {
  .animation-mask {
    --animation-offset: 300px;
  }
}

